.advantages {
  width: 100%;
  margin-bottom: 120px;
  color: $color-black-pearl;

  @include vp-1023 {
    margin-bottom: 75px;
  }

  @include vp-767 {
    margin-bottom: 47px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  list-style-type: none;

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  color: $color-deep-sky-blue;
  min-height: 300px;
  padding: 38px 45px 15px 40px;
  border: 1px solid $color-gray;
  border-left: 5px solid $color-deep-sky-blue;
  transition: border-color $trans-default, background-color $trans-default;

  @include hover-focus {
    background-color: $color-deep-sky-blue;
    border-color: $color-deep-sky-blue;
    cursor: pointer;
  }

  @include vp-1023 {
    min-height: 360px;
    padding-right: 35px;
    padding-bottom: 30px;
    padding-left: 40px;
  }

  @include vp-767 {
    min-height: 340px;
    padding: 30px 30px 35px 25px;
  }

  &--mobile {
    @include vp-767 {
      min-height: 325px;
      padding-top: 30px;
    }
  }
}

.advantages__item svg {
  margin-bottom: 16px;
  fill: $color-deep-sky-blue;
  transition: fill $trans-default;

  @include vp-1023 {
    margin-bottom: 11px;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;
    margin-bottom: 9px;
  }
}

.advantages__item--mobile svg {
  @include vp-767 {
    margin-bottom: 12px;
  }
}

.advantages__item h3 {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: $ff-roboto-condensed;
  font-size: 24px;
  font-weight: 400;
  line-height: 33px;
  color: $color-deep-sky-blue;
  transition: color $trans-default;

  @include vp-1023 {
    margin-bottom: 10px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__item--mobile h3 {
  @include vp-767 {
    margin-bottom: 12px;
  }
}

.advantages__item p {
  margin: 0;
  color: $color-black-pearl;
  line-height: 21px;
  transition: color $trans-default;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages__item:hover h3,
.advantages__item:hover p {
  color: $color-alice-blue;
}

.advantages__item:hover svg {
  fill: $color-alice-blue;
}
